import React, {useState} from'react';
import './style.css';
import{BrowserRouter as Router,Link, Route,Switch} from "react-router-dom";
import * as Yup from "yup";

    

const FormData = ({AddUsers}) => {

    const [name,setName] =useState('')
    const [surname,setSurname] = useState('')
    const [age,setAge] =useState('')
    const [location,setLocation] = useState('')

    const submitData =(e) => {
        e.preventDefault()
        AddUsers(name,surname,age,location)
    };





    return (


        <>
            <form onSubmit ={submitData}>
                
                <input type="text"value = {name} onChange = {(e) => setName(e.target.value)}placeholder ="Enter your name"autoFocus></input>
                <input type="text"value = {surname} onChange ={(e) => setSurname (e.target.value)}placeholder ="enter your surname"autoFocus></input>
                <input type="text"value = {age} onChange = {(e) => setAge(e.target.value)}placeholder ="Enter your age"autoFocus></input>
                <input type="text"value = {location} onChange = {(e) => setLocation(e.target.value)}placeholder ="Enter your location"autofocus></input>
               
                <button type="submit">Add User</button>
                <button type="delete">Delete</button>
            </form>


        </>


    )





}

export default FormData